<template>
  <div v-if="this.$store.state.showBlockChainLogin" >
          <div class='container' id='login-section'>
          <!--  -->
          <div class="row">
            <div class="col-md-3"></div>
            <div class="col-md-6 col-centered div-middle">      
              <!--  -->
              <div class="beforLogin white-box">
                <div id="tabs">
                    <h2> </h2>
                    <small>      .        .</small><br><br>
                <div class="msg-box" v-if="errorMsg != ''">
                  {{errorMsg}}
                </div><br>
                <div class="inner-box">
                  <div class="proj-submenu">
                        <a class="active"> </a>
                        <a>  </a>
                    </div>
                  <!--   -->
                  <div id="myKeystore">  
                    <!--  -->
                    <div class="form-group">
                        <b>Keystore</b><br>
                      <label for="keystore" id="keystore-button"> </label><br>
                      <input type="file" id="keystore" v-on:change="handleImport()">
                    </div>
                    <!--  -->
                    <div class="form-group">
                      <label for="input-password"></label>
                      <input type="password" class="form-control" id="input-password"  v-model="password" onchange="App.handlePassword()">
                      <p class="help-block" id="message"></p>
                    </div>
                  </div>
                  <div class="info-footer">
                    <button type="button" class="btn btn-primary"  v-on:click="$store.commit('TOGGLE_BLOCKCHAIN_LOGIN')" ></button>
                      <button type="button" class="btn btn-primary" id="submit" v-on:click="handleLoginByKeyStore()" style="
        border-left:1px solid white;"></button>
                    </div>
                  <!--  
                  <div id="myPrivateKey">
                      <div class="form-group">
                        <label for="input-private-key"></label>
                        <input type="password" class="form-control" id="input-private-key">
                      </div>
                      <div class="info-footer">
                        <button type="button" class="btn btn-primary" id="submit" onclick="App.handleLoginByPrivateKey()"></button>
                      </div>
                  </div>-->
                </div>
              </div>
            </div>
            </div>
            <div class="col-md-2"></div>
          </div>
      </div>
  </div>
</template>

<script>
// import axios from 'axios'
import Caver from 'caver-js'
// import contractInfo from './contractInfo'
const cav = new Caver('https://api.baobab.klaytn.net:8651/')

export default {
  name: 'BlockChainLogin',
  data () {
    return {
      errorMsg: '',
      password: ''
    }
  },
  methods: {
    handleImport() {
      const fileReader = new FileReader()
      fileReader.readAsText(event.target.files[0])
      fileReader.onload = (event) => {
        console.log(event)
        try {
          if (!this.checkValidKeystore(event.target.result)) {
            this.errorMsg = '  .'
            return
          }
          this.$store.commit('SET_KEYSTORE', event.target.result)
          // this.auth.keystore = event.target.result
          this.errorMsg = 'Keystore.  .'
          document.querySelector('#input-password').focus()
        } catch (event) {
          this.errorMsg = 'ERROR :' + event
          return
        }
      }
    },
    checkValidKeystore(keystore) {
      const parsedKeystore = JSON.parse(keystore)
    //   keystore      
      const isValidKeystore = parsedKeystore.version &&
      parsedKeystore.id &&
      parsedKeystore.address &&
      parsedKeystore.crypto
      return isValidKeystore
    },
    handleLoginByKeyStore() {
      try {
        const privateKey = cav.klay.accounts.decrypt(this.$store.state.keystore, this.password).privateKey
        this.integrateWallet(privateKey)
        this.$store.state.accessType === 'keystore'
      } catch (e) {
        this.errorMsg = ' !'
      }
    },
    integrateWallet(privateKey) {
       // walletInstance    .
      const walletInstance = cav.klay.accounts.privateKeyToAccount(privateKey)
        // walletInstance wallet    ,    
      cav.klay.accounts.wallet.add(walletInstance)
        // walletInstance     >>      ( ,  )
        //           walletInstance  
      sessionStorage.setItem('walletInstance', JSON.stringify(walletInstance))
      // console.log(walletInstance)
      //   
      this.endState()
    },
    endState() {
      this.$root.$emit('UserMenu') //   
      this.$store.commit('HIDE_BLOCKCHAIN_LOGIN') //  
      this.$swal(' ', '       .', 'success')
    }
  },
  mounted () {}
}
</script>

<style scoped>
    .container {
      background-color:rgba(0,0,0,0.9);
      position:fixed;
      top:50px;
      left:0px;
      width:100%;
      height:100vh;
      z-index:2000;
    }
    .white-box {
        background-color:#242424;
        width:100%;
        padding:20px;
        padding-top:40px;
    }
    .div-middle {
        min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 90vh; /* These two lines are counted as one :-)       */

  display: flex;
  align-items: center;
    }
    
    #tabs h2 {
        padding:0px;
        margin:0px;
        color:white;
        font-size:36px;
        font-weight: 900;
    }
    #tabs small {
        color:gray;
    }
    #tabs {
        color:white;
    }
    .inner-box {
        //padding:10px;
        background-color:#4E4E4E;
    }
    .proj-submenu {
        color:white;
        border-bottom: 1px solid gray;
        width:100%;
        height:35px;
        padding-top:10px;
        padding-left:20px;
        margin-bottom:10px;
    }
    
    .proj-submenu a {
        color:white;
        padding-right:10px;
        padding-left:10px;
        padding-bottom:5px;
        font-weight: 400;
    }
    
    .proj-submenu > .active{
        font-weight: 600;
        border-bottom:solid white 2px;
    }
    #myKeystore {
        padding:20px;
    }
    .msg-box{
        width:100%;
        background-image: linear-gradient(to right, #243949 0%, #517fa4 100%);
        padding:5px;
        padding-left:30px;
    }
    .animated{-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:none!important;animation-fill-mode:none!important;z-index:4000;}
    
    .info-footer button {
        width:calc(50% - 2px);
        border:0px;
        margin:0px;
        border-radius: 0px;
        display:inline;
        border-top:1px solid gray;
        background-color: #4E4E4E;
    }
    
    .info-footer button:hover {
        background-color: #242424;
    }
    #submit {
      // background-color:#E3CA23;    
    }
    
    #input-password {
      background:none;
      border:2px solid #FBC02D;
        color:white;
    }
    #keystore {
        position: absolute; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip:rect(0,0,0,0); border: 0;
    }
    #keystore-button {
        background-color:#FBC02D;
        padding:5px;
        width:100%;
        margin-top:10px;
        font-weight: 400;
        text-align: center;
    }
    #keystore-button:hover {
        background-color:#F9A825;
    }

</style>



